
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
data() {
return {
transitionName: "slide-right",
isFull: false,
isShowNav: true
};
},
methods: {
toPage() {
alert(1);
}
},
components: {
Flexbox,
FlexboxItem,
Grid,
GridItem,
Tabbar,
TabbarItem
},
watch: {
$route(to, from) {
var to_index = -1;
var from_index = -1;

if (to.params["index"] != null) {
to_index = to.params["index"];
} else {
to_index = to.meta.index;
}

if (from.params["index"] != null) {
from_index = from.params["index"];
} else {
from_index = from.meta.index;
}

if (to_index == from_index) {
this.transitionName = "";
return;
}

if (to_index != 0) {
this.isFull = true;
setTimeout(() => {
this.isShowNav = !this.isFull;
}, 300);
} else {
this.isFull = false;
this.isShowNav = !this.isFull;
}


if (to_index > from_index) {

this.transitionName = "slide-left";
} else {
this.transitionName = "slide-right";
}
}
},
computed: {}
};



