var Url = {};
Url.install = function(Vue, options) {

var server = 'http://120.78.162.200:12138/index.php/';
var serverAdmin = server + 'Admin/';
var serverHome = server + 'Home/';

Vue.prototype.$getUrl = function(url) {
if(!url) {
console.warn('url' + url);
return '';
}

var _url;
if(url.indexOf('http') == -1) {
_url = server + url;
} else {
_url = url;
}

_url = _url.replace('index.php/', '');
return _url;

}

Vue.prototype.$server = server;
Vue.prototype.$serverAdmin = serverAdmin;
Vue.prototype.$serverHome = serverHome;

Vue.prototype.$serverUpFile = serverHome + "Use/upFile";
}

var Origin = {};
Origin.install = function(Vue, options) {

Vue.prototype.$get = function(url, data, success, error) {

if(data.token == null) {
data.token = localStorage.token ? localStorage.token : '';
}
if(data.user_id == null) {
data.user_id = localStorage.user_id ? localStorage.user_id : '';
}

if(url.indexOf("http") == -1) {

url = this.$serverHome + url;

}

$.ajax({
url: url,
type: 'get',
data: data,



success(res) {
try {
res = JSON.parse(res);
} catch(error) {
console.error(url + '');
console.error(error);
console.error(res);
if(error) {
error(false, error);
}
return false;
}


if(res.res == -992 || res.res == -991) {
router.push({
replace: true,
path: '/login/login'
});
} else {
if(success) {
success(res);
}
}
}
});

}

Vue.prototype.$post = function(url, data, success, error) {
if(data.token == null) {
data.token = localStorage.token ? localStorage.token : '';
}
if(data.user_id == null) {
data.user_id = localStorage.user_id ? localStorage.user_id : '';
}
if(url.indexOf("http") == -1) {

url = this.$serverHome + url;

}

$.ajax({
url: url,
type: 'post',
data: data,




success(res) {

try {
res = JSON.parse(res);
} catch(error) {
console.error(url + '');
console.error(error);
console.error(res);
if(error) {
error(false, error);
}
return false;
}


if(res.res == -992 || res.res == -991) {

router.push({
replace: true,
path: '/login/login'
});
} else {
if(success) {
success(res);
}
}
}

});

}

Vue.prototype.$getTextCount = function(str) {
if(str == null || str == undefined) {
str = '';
}
return str.length;
}

}
Vue.use(Origin)
Vue.use(Url)



















